.btn_social{
      width: 4.2rem;
      height: 4.2rem;
      display: flex;
      align-items: center;
      justify-content: center;
      background: var(--theme-100);
      border-radius: .4rem;
      color: var(--theme-300);
      position: relative;
      transition: .2s;
}

.btn_social *{
      font-size: 2.2rem;
}

.btn_social:hover{
      background: var(--theme-300);
      color: var(--light);
      box-shadow: 1rem 1rem 5px rgba(0,0,0,.2);
      transform: translate(-.5rem, -.5rem);
}