.home_content{
      width: 100%;
      height: 63rem;
      position: relative;
      z-index: 10;
      margin-inline: auto;
}

.home_headline{
      display: flex;
      width: 100%;
      height: 5rem;
}

.social_media{
      display: flex;
      align-items: center;
      gap: 1rem;
      margin-top: 2.5rem;
}

.user_data__right{
      width: 25rem;
      height: 100%;
      color: var(--theme-300);
      background: linear-gradient(transparent 66px, var(--theme-100) 66px, var(--theme-100) 500px, transparent 500px);
}

.user_info{
      margin-top: 5.4rem;
}

.user_avatar,
.user_avatar__box,
.user_avatar__box img{
      width: 16rem;
      height: 16rem;
      border-radius: 50%;
}

.user_avatar{
      background: var(--theme-200);
      margin-inline: auto;
      box-shadow: 0 0 0 .5rem rgba(0,0,0,.2);
      position: relative;
}

.user_avatar a{
      position: absolute;
      width: 4.2rem;
      height: 4.2rem;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      background: var(--light);
      color: var(--theme-300);
      bottom: -1rem;
      left: 2rem;
}

.user_avatar a *{
      font-size: 2.2rem;
}

.user_about{
      width: 22rem;
      height: 18rem;
      margin-top: 2rem;
      margin-inline: auto;
      overflow-x: hidden;
      overflow-y: auto;
      scrollbar-width: thin;
      scrollbar-color: rgba(0,0,0,.05) var(--theme-300);
}

.user_about p{
      font-size: 1.8rem;
}

.home_title{
      position: relative;
}

.user_title__name{
      position: relative;
      height: fit-content;
}

.user_title__name h1,
.user_title__name h2{
      width: fit-content;
      height: fit-content;
      font-size: 14rem;
      line-height: 11rem;
      text-transform: uppercase;
      font-family: var(--ft-3);
      font-weight: 800;
      position: absolute;
      top: 0;
      left: 0;
      letter-spacing: .5rem;
}

.user_title__name h1{
      color: transparent;
      background: linear-gradient(var(--theme-300) 40%, var(--light) 40%, var(--light) 60%, var(--theme-300) 60%);
      -webkit-background-clip: text;
      -moz-background-clip: text;
      background-clip: text;
      z-index: 1;
}

.user_title__name h2{
      -webkit-text-stroke: 1rem var(--theme-100);
      -moz-text-stroke: 1rem var(--theme-100);
      text-shadow: 1rem 1rem 10px rgba(0,0,0,.1);
}

.home_title p{
      margin-toP: 12rem;
      display: inline-block;
      font-size: 2rem;
      color: var(--theme-300);
}

.navigate{
      width: 100%;
      height: auto;
      display: flex;
      align-items: center;
      position: absolute;
      top: 20rem;
      left: 50%;
      transform: translateX(-50%);
      justify-content: space-between;
}

.btn_navigator{
      width: 20rem;
      height: 20rem;
      background: transparent;
      border-radius: 1rem;
      position: relative;
}

.btn_navigator:hover{
      animation: pulse .5s ease-in-out forwards;
}

@keyframes pulse{
      0%{
            transform: translateY(0);
      }
      10%{
            transform: translateY(1rem);
      }
      20%{
            transform: translateY(-1rem);
      }
      30%{
            transform: translateY(1rem);
      }
      40%{
            transform: translateY(-1rem);
      }
      50%{
            transform: translateY(1rem);
      }
      60%{
            transform: translateY(-1rem);
      }
      70%{
            transform: translateY(1rem);
      }
      80%{
            transform: translateY(-1rem);
      }
      90%{
            transform: translateY(1rem);
      }
      0%{
            transform: translateY(0);
      }
}
