
.background{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      overflow: hidden;
}

.bubble{
      position: absolute;
      border-radius: 50%;
      filter: blur(50px);
}

.bubble:nth-child(1),
.bubble:nth-child(4){
      width: 48rem;
      height: 48rem;
      background: var(--theme-300);
}

.bubble:nth-child(1){
      top: 0;
      left: 0;
      transform: translate(-30%, -20%);
}

.bubble:nth-child(4){
      bottom: 0;
      right: 0;
      transform: translate(30%, 20%);
}

.bubble:nth-child(2),
.bubble:nth-child(3){
      background: var(--light);
      width: 40rem;
      height: 40rem;
}

.bubble:nth-child(2){
      left: 60%;
      top: 20%;
}

.bubble:nth-child(3){
      left: 20%;
      top: 60%;
}