.online_content{
      display: flex;
      gap: 1rem;
      border-radius: 999px;
      position: absolute;
      top: 0;
      left: 0;
}

.online_avatar,
.online_avatar img{
      width: 3.2rem;
      height: 3.2rem;
      border-radius: 999px;
}

.online_avatar{
      box-shadow: 0 0 0 .3rem rgba(255,255,255,.6);
}

.online_info{
      padding: 0 2rem;
      border-radius: 999px;
      background: var(--light);
}

.online_content span{
      width: fit-content;
      display: block;
      word-break: normal;
      text-transform: uppercase;
      font-weight: 500;
      font-size: 1.4rem;
      line-height: 3.2rem;
}

.online_content span.online{
      color: var(--green);
}

.online_content span.offline{
      color: var(--red-200);
}