.modal_page{
      position: fixed;
      width: 100%;
      height: 100%;
      background: rgba(255,255,255,.3);
      top: 0;
      left: 0;
      -webkit-backdrop-filter: blur(10px);
      -moz-backdrop-filter: blur(10px);
      backdrop-filter: blur(10px);
      z-index: 500;
      opacity: 0;
      visibility: hidden;
      display: grid;
      place-content: center;
}

.modal_page.show{
      animation: show_modal .3s ease-in-out forwards;
}

@keyframes show_modal{
      100%{
            opacity: 1;
            visibility: visible;
      }
}

.page_content{
      width: 73rem;
      height: 52rem;
      display: flex;
      position: relative;
      transform: translateX(100%);
      opacity: 0;
}

.page_content.show{
      animation: show_page .5s ease-in-out forwards;
      animation-delay: .3s;
}

@keyframes show_page{
      100%{
            opacity: 1;
            transform: translateX(0);
      }
}

.title_page__content{
      width: 15rem;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
}

.title_page__content h2{
      height: 100%;
      position: relative;
      transform: translateY(-2rem);
      display: flex;
      justify-content: center;
      writing-mode: vertical-rl;
      text-orientation: upright;
      text-transform: uppercase;
      font-size: 9rem;
      color: var(--theme-300);
      font-family: var(--ft-1);
      letter-spacing: -4rem;
}

.wraper_pages{
      width: 585rem;
      height: 100%;
      background: var(--light);
      position: relative;
      padding: 2.5rem;
}

.close_page{
      width: 4.2rem;
      height: 4.2rem;
      background: var(--theme-300);
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(50%, -50%);
      transition: .5s;
}

.close_page *{
      color: var(--light);
      font-size: 1.6rem;
}

.close_page:hover{
      transform: translate(50%, -50%) rotate(360deg);
}

.page_inner{
      width: 100%;
      height: 100%;
      overflow-x: hidden;
      overflow-y: auto;
      scrollbar-width: thin;
      scrollbar-color: rgba(0,0,0,.05) var(--theme-300);
}

/****
* ABOUT
****/

.about_headline{
      height: 28rem;
      width: 100%;
      display: flex;
      gap: 2.5rem;
}

.about_avatar,
.about_avatar img{
      width: 25rem;
      height: 25rem;
      border-radius: .6rem;
}

.about_avatar{
      box-shadow: 2rem 3rem 20px rgba(0,0,0,.1);
}

.about_info h3{
      text-transform: capitalize;
      font-family: var(--ft-3);
      color: var(--theme-300);
      font-size: 5rem;
}

.about_info span{
      color: var(--theme-300);
      font-size: 1.6rem;
}

.about_info strong{
      color: rgba(0,0,0,.1);
      display: block;
      font-size: 4rem;
}

.about_location{
      display: flex;
      align-items: center;
      gap: .5rem;
}

.about_location *{
      font-size: 2rem;
      color: var(--theme-300);
      fill: var(--theme-300);
      stroke: .2rem var(--theme-300);
}

.about_description{
      margin-top: 2rem;
      padding-top: 2rem;
      border-top: solid .1rem var(--theme-100);
}

.about_description p{
      color: var(--dark-100);
      font-size: 1.6rem;
}

/***
* FRIEND
**/

.friend_content{
      width: 100%;
      height: auto;
      display: flex;
      gap: 3rem;
      flex-wrap: wrap;
}

.friend_card,
.friend_card img{
      width: 14rem;
      height: 16rem;
      border-radius: .6rem;
}

.friend_card img{
      filter: grayscale(1);
      transition: .2s;
}

.friend_card:hover img{
      filter: grayscale(0);
}

.friend_card{
      position: relative;
      box-shadow: 2rem 3rem 25px rgba(0,0,0,.2);
      overflow: hidden;
}

.friend_card:before{
      width: 100%;
      height: 100%;
      background: linear-gradient(transparent, var(--theme-100));
      
}

.friend_card a{
      display: block;
      width: fit-content;
      line-height: 3rem;
      padding: 0 1.5rem;
      text-transform: uppercase;
      position: absolute;
      left: 50%;
      bottom: 2rem;
      transform: translate(-50%, 200%);
      transition: .2s ease-in-out;
      background: var(--theme-300);
      color: var(--light);
      font-size: 1.2rem;
      border-radius: .4rem;
      box-shadow: 1rem 1rem 10px rgba(0,0,0,.2);
}

.friend_card a:hover{
      background: var(--light);
      color: var(--theme-300);
}

.friend_card:hover a{
      transform: translate(-50%, 0);
}

/***
* RELATION
**/

.relation_headline{
      width: 100%;
      height: 18rem;
      display: flex;
      gap: 2.5rem;
}

.relation_avatar,
.relation_avatar img{
      width: 16rem;
      height: 16rem;
      border-radius: .6rem;
}

.relation_avatar{
      box-shadow: 2rem 2rem 20px rgba(0,0,0,.2);
}

.relation_info h2{
      font-size: 3.5rem;
      color: var(--theme-300);
}

.relation_info strong{
      display: block;
      color: rgba(0,0,0,.3);
      font-size: 1.8rem;
}

.relation_info a{
      line-height: 2rem;
      background: var(--light);
      color: var(--theme-300);
      border: solid .1rem var(--theme-200);
      border-radius: .3rem;
      padding: 0 1rem;
      margin-top: 2rem;
      text-transform: uppercase;
      transition: .2s;
}

.relation_info a:hover{
      background: var(--theme-300);
      color: var(--light);
}

.relation_description{
      margin-top: 2rem;
      border-top: solid .1rem var(--theme-100);
      padding-top: 2rem;
}

.relation_description p{
      color: var(--dark-100);
      font-size: 1.8rem;
}